@import "vars";
@import "libs";
@import "base";

.link {
  color: inherit;
  text-decoration: none;

  &:hover {
    color: $color-primary;
  }
}

.btn {
  padding: 10px;

  display: flex;
  align-items: center;
  justify-content: center;

  font-family: inherit;
  text-decoration: none;
  color: $color-primary;
  text-transform: uppercase;

  border: 2px solid $color-primary;
  cursor: pointer;

  &:hover {
    color: $color-light;

    background: $color-primary;
  }

  &:disabled {
    color: $color-light;

    border-color: $color-light;
    background: $color-shadow;
    pointer-events: none;
  }
}

.gh-link {
  position: relative;

  padding-left: 40px;

  &::before {
    content: url(../assets/icons/gh.svg);

    width: 30px;
    height: 30px;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.header {
  padding: 15px 0;

  &__inner {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
  }
}

.logo {
  width: 200px;
  margin-bottom: 15px;
}

.nav {
  max-width: 100%;
  overflow: auto;

  padding: 20px 0;

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    margin: 0 15px;

    cursor: pointer;
  }

  &__link {
    font-family: inherit;
    font-size: inherit;
    text-decoration: none;
    color: $color-dark;
    white-space: nowrap;

    padding: 15px 7px;

    &:hover {
      color: $color-light;

      background: $color-primary;
    }
  }
}

.footer {
  padding: 10px 0;

  &__inner {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
  }
}

.school-logo {
  width: 200px;
  margin: 15px;
}

.copy {
  font-weight: 300;

  opacity: 0.6;
}

.player {
  width: 100%;

  display: flex;
  align-items: center;

  &__btn {
    margin-right: 15px;

    background: $color-primary;
    border-radius: 50%;
  }

  &__icon {
    width: 25px;
  }

  &__audio {
    display: none;
  }

  &__progress-container {
    width: 100%;

    display: flex;
    flex-direction: column;
  }

  &__progress {
    width: 100%;

    padding: 1px;

    -webkit-appearance: none;
    cursor: pointer;
    height: 7px;
    background: #FDFDFD;
    border-radius: 5px;
    background-image: linear-gradient($color-primary, $color-primary);
    border: 1px solid $color-primary;
    background-size: 0;
    background-repeat: no-repeat;

    &::-webkit-slider-thumb {
      width: 15px;
      height: 15px;

      -webkit-appearance: none;
      border-radius: 50%;
      background: $color-light;
      box-shadow: 0 0 2px 0 $color-shadow;
      transition: background .3s ease-in-out;
    }

    &::-webkit-slider-runnable-track {
      -webkit-appearance: none;
      box-shadow: none;
      border: none;
      background: transparent;
    }
  }

  &__time {
    width: 100%;

    display: flex;
    justify-content: space-between;
  }

  &__volume {
    position: relative;

    width: 100px;

    margin-bottom: 15px;

    align-self: end;

    -webkit-appearance: none;
    cursor: pointer;
    border-radius: 20px;
    background: transparent;

    &::before {
      content: "";

      position: absolute;
      top: -50%;
      right: 100%;
      transform: translateX(-50%);

      width: 20px;
      height: 20px;

      background: url('../assets/icons/player/volume.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      pointer-events: none;
    }

    &:focus {
      outline: none;
    }

    &::-webkit-slider-runnable-track {
      width: 100%;
      height: 7px;
      cursor: pointer;
      animate: 0.2s;
      background: $color-primary;
      border-radius: 20px;
    }

    &::-webkit-slider-thumb {
      width: 15px;
      height: 15px;
      margin-top: -4px;

      box-shadow: 0 0 2px 0 $color-shadow;
      border-radius: 50%;
      background: $color-light;
      cursor: pointer;
      -webkit-appearance: none;
    }

    &:focus::-webkit-slider-runnable-track {
      background: $color-primary;
    }

    &::-moz-range-track {
      width: 100%;
      height: 9px;
      cursor: pointer;
      animate: 0.2s;
      background: $color-primary;
      border-radius: 20px;
    }

    &::-moz-range-thumb {
      border: 0px solid $color-primary;
      height: 15px;
      width: 15px;
      border-radius: 50%;
      cursor: pointer;
    }

    &::-ms-track {
      width: 100%;
      height: 9px;
      cursor: pointer;
      animate: 0.2s;
      background: transparent;
      border-color: transparent;
      color: transparent;
    }

    &::-ms-fill-lower {
      background: $color-primary;
      border-radius: 20px;
    }

    &::-ms-fill-upper {
      background: $color-primary;
      border-radius: 20px;
    }

    &::-ms-thumb {
      margin-top: 1px;
      border: 0px solid $color-primary;
      height: 15px;
      width: 15px;
      border-radius: 15px;
      cursor: pointer;
    }

    &:focus::-ms-fill-lower {
      background: $color-primary;
    }

    &:focus::-ms-fill-upper {
      background: $color-primary;
    }
  }
}

.birds {
  &__items {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: stretch;
  }
}

.card {
  width: 100%;
  max-width: 300px;
  min-height: 100%;

  margin: 30px;

  background: #FDFDFD;
  box-shadow: 0px 1px 8px 0px rgba(34, 60, 80, 0.2);

  &__media {
    width: 100%;
    height: 200px;
    overflow: hidden;

    margin-bottom: 15px;
  }

  &__img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
  }

  &__header,
  &__body {
    padding: 10px 15px;
  }

  &__name {
    font-size: 24px;
    line-height: 1;
  }

  &__species {
    font-size: 14px;
    font-weight: 300;
  }

  &__body {
    position: relative;

    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__description {
    margin-bottom: 10px;

    font-size: 16px;
    line-height: 1.5;
  }
}

.intro {
  background: url(../assets/images/intro/bg.png);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__img {
    max-width: 100%;

    margin-bottom: 15px;
  }

  &__text {
    max-width: 420px;

    margin-bottom: 20px;

    font-size: 24px;
    line-height: 1.3;
    text-align: center;
  }

  &__btns {
    width: 100%;
    max-width: 250px;

    display: flex;
    justify-content: space-around;
  }
}

.question {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__card {
    text-align: center;
  }
}

.options-container {
  max-width: 300px;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.options {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;

  &__item {
    position: relative;

    width: 100%;

    padding: 5px 5px 5px 30px;

    cursor: pointer;

    &:not(:first-child) {
      border-top: 1px solid $color-primary;
    }

    &::before {
      content: '';

      position: absolute;
      top: 50%;
      left: 15px;
      transform: translate(-50%, -50%);

      width: 15px;
      height: 15px;

      border: 1px solid $color-primary;
      border-radius: 50%;
    }

    &_error::before {
      border-color: $color-error;
      background: $color-error
    }

    &_correct::before {
      background: $color-primary;
    }
  }
}

.answer {
  &__inner {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
  }
}

.quizbar {
  margin-bottom: 30px;

  display: flex;
  justify-content: center;

  @media (max-width: 925px) {
    display: block;
  }

  &__inner {
    width: 100%;
    overflow: auto;

    margin: 0 auto;

    display: flex;
    align-items: center;
  }

  &__item {
    padding: 5px 15px;

    font-size: 18px;
    font-weight: 700;
    color: $color-primary;
    white-space: nowrap;

    background: $color-light;
    border: 1px solid $color-primary;
    border-top-width: 2px;
    border-bottom-width: 2px;

    &:last-child {
      border-right-width: 2px;
    }

    &:first-child {
      border-left-width: 2px;
    }

    &_active {
      color: $color-light;

      background: $color-primary;
      border-color: $color-primary;
    }
  }
}

@import "media";